import Vue from 'vue'
import wx from 'weixin-js-sdk'
import { Toast } from 'vant'
import { hostUrl } from '../../components/Global.vue'

Vue.use(Toast)

function axiosRequest(method, url, params, data, success, fail) {
  Vue.prototype
    .$ajax({
      method: method,
      url: url,
      headers: { 'Access-Control-Allow-Origin': '*','from': 'h5' },
      dataType: 'jsonp',
      data,
      params
    })
    .then(res => {
      if (res.data.errno === 0) {
        success(res)
      }else{
        console.log('test',res.data.errmsg)
        fail(res)
        
        Toast(res.data.errmsg)
        //
      }
    })
    .catch(res => {
      Toast(res.response.data.errmsg)
      // console.log(res.response.data.errmsg)
      if (res.response.status == 401) {
        let param = ``
        // getCode(param)
      }
      if (typeof fail == 'function') {
        fail(res)
      }
    })
}
function getCode(param) {
  // const redirect_url = encodeURIComponent(`https://pdz.sync163.com/webpage/`)
  let redirect_url
  if(param){
    redirect_url = encodeURIComponent(param)
  }else{
    redirect_url = encodeURIComponent(`https://kz.sync163.com/webpage/`)
    // redirect_url = encodeURIComponent(`http://dev.kz.sync163.com/webpage/`)
  }
  
  const appId = 'wxbcc23d04c3aa4a7c'
  const response_type = 'code'
  const scope = 'snsapi_userinfo'
  const state = 11
  let url = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=${appId}&redirect_uri=${redirect_url}&response_type=${response_type}&scope=${scope}&state=${state}#wechat_redirect`
  window.location.href = url
}
function login(data) {
  return new Promise((resolve,reject)=>{
    axiosRequest(
      'POST',
      'gzh/login',
      '',
      data,
      res => {
        console.log(res)
        // localStorage.setItem('userInfo',res.data.userinfo)
        Vue.prototype.$global.userInfo = res.data.userinfo
        resolve(res)
      },
      res => {
        console.log(res)
        reject()
      }
    )
  })
}
function payRequest(timestamp, nonceStr, pack, signType, paySign, success) {
  console.log(typeof timestamp)
  wx.chooseWXPay({
    timestamp, // jssdktimestamptimeStampS
    nonceStr, //  32 
    package: pack, // prepay_idprepay_id=\*\*\*
    signType, // 'SHA1''MD5'
    paySign, // 
    success: function(res) {
      if (typeof success == 'function') {
        success(res)
      }
      // 
    }
  })
}
function wxConfig(success,data) {
  axiosRequest(
    'POST',
    'gzh/jssdk/config',
    '',
    data,
    res => {
      console.log(res)
      wx.config({
        debug: false, // ,apialertpclogpc
        appId: res.data.payload.config.appId, // 
        timestamp: res.data.payload.config.timestamp, // 
        nonceStr: res.data.payload.config.nonceStr, // 
        signature: res.data.payload.config.signature, // 
        jsApiList: res.data.payload.config.jsApiList // JS
      })
      wx.ready(function() {
        // alert('ready')
        if (typeof success == 'function') {
          success()
        }
      })
    },
    res => {}
  )
}

export default {
  axiosRequest,
  getCode,
  payRequest,
  login,
  wxConfig
}
